}

.header-nav li a {
  font-size: 15px;
  font-weight: 500;
  color: #1F283C;
  line-height: 20px;
}

.hamburger {
  display: none;
  position: relative;
  width: 32px;
  height: 33px;
  padding: 4.5px;
  overflow: hidden;
}

.hamburger:before, .hamburger:after {
  display: block;
  content: '';
  width: 23px;
  height: 1.5px;
  background-color: #23A8CD;
  position: absolute;
  top: 4.5px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  transition: .4s all ease;
}

.hamburger:after {
  top: unset;
  bottom: 4.5px;
}

.hamburger .hamburger-inner {
  width: 23px;
  height: 1.5px;
  background-color: #23A8CD;
  position: absolute;
  top: 0;
  bottom: 0;
  margin-top: auto;
  margin-bottom: auto;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
}

.hamburger.active:before {
  transform: rotate(-45deg);
  top: 14px;
  left: -1px;
  position: absolute;
}

.hamburger.active:after {
  transform: rotate(45deg);
  top: 14px;
  left: -1px;
  position: absolute;
}

.hamburger.active .hamburger-inner {
  transform: translateX(200%);
}

.mobile-menu {
  display: none;
  position: fixed;
  left: 0;
  top: 80px;
  width: 100%;
  background-color: #fff;
  padding: 15px 20px 30px;
  height: calc(100vh - 80px);
  z-index: 999;
}

.mobile-menu.fixed {
  height: calc(100vh - 69px);
  top: 69px;
}

.mobile-menu .mobile-filter {
  margin: 25px 0;
  box-shadow: none;
  border: 1px solid #E9E9E9;
  position: relative;
}

.mobile-menu .mobile-filter:after {
  display: block;
  content: '';
  width: 100%;
  height: 2px;
  background: #E9E9E9;
  position: absolute;
  top: -25px;
  left: 0;
  bottom: unset;
}

.mobile-menu nav ul li {
  margin-bottom: 15px;
}

.mobile-menu nav ul li:last-child {
  margin-bottom: 0;
}

.mobile-menu nav ul li a {
  font-size: 21px;
  font-weight: 500;
  color: #1F283C;
  line-height: 30px;
}

.mobile-menu .menu-bottom {
  position: absolute;
  bottom: 30px;
  padding-top: 25px;
  border-top: 2px solid #E9E9E9;
  width: calc(100% - 40px);
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
}

.mobile-menu .menu-bottom .copyright {
  font-size: 14px;
  color: #797C84;
  line-height: 20px;
  margin-bottom: 8px;
}

.mobile-menu .menu-bottom .developed {
  font-size: 14px;
  color: #797C84;
  line-height: 20px;
}

.mobile-menu .menu-bottom .developed a {
  color: #1F283C;
  font-weight: 500;
}

@media screen and (max-width: 990px) {
  .header-logo {
    max-width: 180px;
  }
  .header-logo .logo-titles .top {
    font-size: 11px;
  }
  .header-logo .logo-titles .bottom {
    font-size: 12px;
  }
  .header-nav li {
    margin-right: 10px;
  }
}

@media screen and (max-width: 500px) {
  header {
    padding: 15px 0;
  }
  .header-logo {
    max-width: 275px;
  }
  .header-logo a img {
    max-width: 35px;
  }
  .header-logo .logo-titles {
    width: calc(100% - 45px);
  }
  .header-logo .logo-titles .top {
    font-size: 16px;
  }
  .header-logo .logo-titles .bottom {
    font-size: 13px;
  }
  .header-nav {
    display: none;
  }
  .hamburger {
    display: block;
  }
