@media screen and (max-width: 350px) {
  .header-logo {
    max-width: 200px;
  }
  .header-logo .logo-titles .top {
    font-size: 16px;
  }
  .mobile-menu {
    top: 69px;
    height: calc(100vh - 69px);
  }
  .mobile-menu nav ul li {
    margin-bottom: 10px;
  }
}

